/* CSS Variables - Dark theme default for DEX */
:root {
  --radius: 0.75rem;

  /* Light theme (not primary for DEX) */
  --background: 0 0% 100%;
  --foreground: 240 10% 3.9%;
  --card: 0 0% 100%;
  --card-foreground: 240 10% 3.9%;
  --popover: 0 0% 100%;
  --popover-foreground: 240 10% 3.9%;
  --primary: 240 5.9% 10%;
  --primary-foreground: 0 0% 98%;
  --secondary: 240 4.8% 95.9%;
  --secondary-foreground: 240 5.9% 10%;
  --muted: 240 4.8% 95.9%;
  --muted-foreground: 240 3.8% 46.1%;
  --accent: 240 4.8% 95.9%;
  --accent-foreground: 240 5.9% 10%;
  --destructive: 0 84.2% 60.2%;
  --destructive-foreground: 0 0% 98%;
  --border: 240 5.9% 90%;
  --input: 240 5.9% 90%;
  --ring: 240 5.9% 10%;

  /* Chart colors */
  --chart-1: 142.1 76.2% 36.3%;
  --chart-2: 217.2 91.2% 59.8%;
  --chart-3: 316.6 73.3% 69.4%;
  --chart-4: 38.3 95.8% 56.7%;
  --chart-5: 0 84.2% 60.2%;

  /* Exchange-specific */
  --swap-module: 0 0% 100%;
  --swap-module-foreground: 240 10% 3.9%;

  /* Status colors */
  --success: 142.1 76.2% 36.3%;
  --success-foreground: 0 0% 98%;
  --warning: 38.3 95.8% 56.7%;
  --warning-foreground: 0 0% 0%;
  --info: 217.2 91.2% 59.8%;
  --info-foreground: 0 0% 98%;
}

/* Dark theme - Primary for DEX */
.dark {
  --background: 240 10% 3.9%;
  --foreground: 0 0% 98%;
  --card: 240 10% 3.9%;
  --card-foreground: 0 0% 98%;
  --popover: 240 10% 3.9%;
  --popover-foreground: 0 0% 98%;
  --primary: 0 0% 98%;
  --primary-foreground: 240 5.9% 10%;
  --secondary: 240 3.7% 15.9%;
  --secondary-foreground: 0 0% 98%;
  --muted: 240 3.7% 15.9%;
  --muted-foreground: 240 5% 64.9%;
  --accent: 240 3.7% 15.9%;
  --accent-foreground: 0 0% 98%;
  --destructive: 0 62.8% 30.6%;
  --destructive-foreground: 0 0% 98%;
  --border: 240 3.7% 15.9%;
  --input: 240 3.7% 15.9%;
  --ring: 240 4.9% 83.9%;

  /* Chart colors - dark mode */
  --chart-1: 142.1 70.6% 45.3%;
  --chart-2: 217.2 91.2% 59.8%;
  --chart-3: 316.6 73.3% 52%;
  --chart-4: 38.3 95.8% 56.7%;
  --chart-5: 0 84.2% 60.2%;

  /* Exchange-specific - dark */
  --swap-module: 240 5% 12%;
  --swap-module-foreground: 0 0% 98%;

  /* Status colors - dark */
  --success: 142.1 70.6% 45.3%;
  --success-foreground: 0 0% 98%;
  --warning: 38.3 95.8% 56.7%;
  --warning-foreground: 0 0% 0%;
  --info: 217.2 91.2% 59.8%;
  --info-foreground: 0 0% 98%;
}

